<template>
    <el-upload
      class="upload"
      action="/admin/media/add"
      :on-preview="handlePreview"
      :headers="setHeaders"
      :on-remove="handleRemove"
      :before-remove="beforeRemove"
      :before-upload="beforeUpload"
      :on-error="uploadError"
      :on-success="uploadSuccess"
      name="image"
      multiple
      :limit="10"
      :on-exceed="handleExceed"
      :file-list="fileList">
      <el-button size="small" type="primary"></el-button>
      <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
    </el-upload>
</template>

<script>
  export default {
    name: 'MediaAdd',
    data () {
      return {
        setHeaders: {
          Authorization: `Bearer ${this.$store.state.token}`
        },
        id: '',
        picname: '',
        fileList: []
      }
    },
    beforeMount () {
      this.id = this.$route.params._id
      console.log(this.id)
    },
    methods: {
      handleRemove  (file, fileList) {  // 
        console.log('handleRemove:')
        console.log('file')
        console.log(file)
        console.log('fileList')
        console.log(fileList)
        return false
      },
      beforeRemove (file, fileList) {  // 
        console.log('beforeRemove:')
        console.log('file')
        console.log(file)
        let that = this
        let id = file.response.data
        let promise = new Promise(function (resolve, reject) {
          that.$axios.get('/admin/media/delete/' + id).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              that.$message({type: 'success', message: ''})
              resolve()
            } else {
              that.$message({type: 'error', message: ''})
              reject(new Error(''))
            }
          })
        })
        return promise
      },
      handlePreview (file) { // ,  file.response 
        console.log('handlePreview:')
        console.log(file)
      },
      handleExceed (files, fileList) { // 
        this.$message.warning(` 10  ${files.length}  ${files.length + fileList.length} `)
      },
      // 
      uploadSuccess (response, file, fileList) {
        this.$message({message: '', type: 'success'})
        console.log('response:')
        console.log(response)
        console.log('file')
        console.log(file)
        console.log('fileList')
        console.log(fileList)
      },
      // 
      uploadError (err, file, fileList) {
        this.$message({message: '', type: 'error'})
        console.log(err)
      },
      beforeUpload (file) {
        let isJPG = (file.type === 'image/jpeg' || file.type === 'image/png')
        let isLt2M = file.size / 1024 / 1024 < 2

        if (!isJPG) {
          this.$message.error(' jpg,png !')
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!')
        }
        return isJPG && isLt2M
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .upload {
    max-width: 500px;
  }
</style>



